import Evented from '@scanex/evented';
import * as Request from './Request.js';
import * as Translation from '@scanex/translations';
import en from './strings.en.json';
import ru from './strings.ru.json';

const NOTIFY_TIMEOUT = 5000;

const translate = Translation.translate;

Translation.add('ru', ru);
Translation.add('en', en);

export default class Controller extends Evented {
    constructor() {
        super();        
    }    
    get notification() {
        return this._notification;
    }
    set notification(notification) {
        this._notification = notification;
    }
    async _httpGet(url, options) {
        const {status, result} = await Request.httpGet(url, options);
        const ok = this._status(status);
        return {ok, result};
    }
    async _httpPost(url, options) {
        const {status, result} = await Request.httpPost(url, options);
        const ok = this._status(status);
        return {ok, result};
    }
    _status(status) {        
        switch(status) {
            case 200:
                this._notification && this._notification.info(translate('notify.info'), NOTIFY_TIMEOUT);
                return true;
            case 400:
                this._notification && this._notification.error(translate('notify.error.badrequest'), NOTIFY_TIMEOUT);
                return false;
            case 401:
                this._notification && this._notification.error(translate('notify.error.unauthorized'), NOTIFY_TIMEOUT);
                return false;
            case 403:
                this._notification && this._notification.error(translate('notify.error.forbidden'), NOTIFY_TIMEOUT);
                return false;
            case 404:
                this._notification && this._notification.error(translate('notify.error.notfound'), NOTIFY_TIMEOUT);
                return false;
            case 500:
                this._notification && this._notification.error(translate('notify.error.server'), NOTIFY_TIMEOUT);
                return false;
            case 502:
                this._notification && this._notification.error(translate('notify.error.gateway'), NOTIFY_TIMEOUT);
                return false;
            case 503:
                this._notification && this._notification.error(translate('notify.error.unavailable'), NOTIFY_TIMEOUT);
                return false;
            default:
                this._notification && this._notification.error(translate('notify.error.other'), NOTIFY_TIMEOUT);
                return false;
        }
    }
};